import { CUSTOMER_SELECTED, FETCH_CUSTOMERS_FAILURE, FETCH_CUSTOMERS_REQUEST, FETCH_CUSTOMERS_SUCCESS, SAVE_CUSTOMER_FAILURE, SAVE_CUSTOMER_REQUEST, SAVE_CUSTOMER_SUCCESS, UPDATE_SELECTED_CUSTOMER } from "store/types/actionTypes";
const initialState = {
    loading: false,
    customers: [],
    error: null,
    selectedCustomer: null
};
export const customerReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_CUSTOMERS_REQUEST:
            return Object.assign(Object.assign({}, state), { loading: true });
        case FETCH_CUSTOMERS_FAILURE:
            return Object.assign(Object.assign({}, state), { loading: false });
        case FETCH_CUSTOMERS_SUCCESS:
            return Object.assign(Object.assign({}, state), { loading: false, customers: action.payload });
        case SAVE_CUSTOMER_REQUEST:
            return Object.assign(Object.assign({}, state), { loading: true });
        case SAVE_CUSTOMER_SUCCESS:
            return Object.assign(Object.assign({}, state), { selectedCustomer: action.payload, error: null, loading: false });
        case SAVE_CUSTOMER_FAILURE:
            return Object.assign(Object.assign({}, state), { selectedCustomer: null, error: action.payload, loading: false });
        case CUSTOMER_SELECTED:
            return Object.assign(Object.assign({}, state), { selectedCustomer: action.payload });
        case UPDATE_SELECTED_CUSTOMER:
            return Object.assign(Object.assign({}, state), { selectedCustomer: action.payload });
        default:
            return state;
    }
};
